import './Projects.css';
import React , {useEffect} from "react"
import Aos from "aos";
import "aos/dist/aos.css";

export default function Project(props){
    useEffect(() => {
        Aos.init({duration:1500});
     }, []);
    const name = props.name
    const image = props.image
    const descrip = props.descrip
    const link = props.link
    return (
        <div class="col-lg-4 my-4">
        <div data-aos="fade-up" data-aos-once="false" class="card h-100 border border-white my-3 mx-4">
            <img src={image} class="card-img-top" alt="pic"></img>
        <div class="card-body">
            <h5 class="card-title projName px-3 pt-3">{name} </h5>
            <p class="card-text projDesc px-3 pb-0">{descrip}</p>
        </div>
        <div class="card-footer bg-white border border-white text-center pt-0">
            <a href={link} class="btn btn-primary mb-3 ml-5 w-75" target=" blank">Learn More!</a>
        </div>
        </div>
        </div>
    )
}